/**
 * File Manager Drag and Drop feature sample
 */
export class PortfolioPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {isExtended: "extended", page: "main"}
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({isExtended: "not-extended"})
    }
    else {
      this.setState({isExtended: "extended"})
    }
  }
  openOtherPage = () => {
    this.setState({page: "master"})
  }
  render() {
    return (
      <>
        {(this.state.page === "master") ? <PortfolioMasterPage /> : (<><div style={{ position: "absolute" }}>
          <Navbar pageName="Your Portfolio" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
            </div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {overflow: "hidden"} : {width: "100vw", overflow: "hidden"})}>
          <div style={{marginLeft: "40px"}}>
            <PointTrackers />
          </div>
          <div style={{width: "100%", height: "100%", position: "relative", marginTop: "3%"}}>
          <div style={{width: "70%", height: "100%", marginTop: "2%",marginLeft: "3%"}}>
            <h1 style={{marginLeft: "1%", }}>
              Previous Projects
            </h1>
            <div style={{height: "70%"}}>
            
              <CustomChonky />
              
          </div>
          </div>
          <div style={{position: "absolute", height: "85%", width: "22%", backgroundColor: "#f1f1f1", top: 0, right: "3%", paddingTop: "20px", borderRadius: "20px"}}>
          <div style={{display: "flex", justifyContent: "center", height: "100%", width: "100%"}}>
            <div style={{height: "100%", width: "80%"}}>
            <DownloadMasterButton text={"Download"}/>
            <DownloadMasterButton text={"Master"} openOtherPage={this.openOtherPage}/>
            <DownloadMasterButton text={"Knowledge Nuggets"}/>
            </div>
            </div>
      </div>
          </div>
          </div>
          <div>
          </div>
        </div></>)}
      </>
    )
  }
}
class DownloadMasterButton extends React.Component {
  constructor(props) {
    super(props)
    this.state = {redirect: null}
  }
  render() {
    return (
      <div style={{width: "100%", height: "25%", marginBottom: "15%", cursor:"pointer" }} onClick={this.props.openOtherPage}>
        <div style={{width: "100%", height: "30%", backgroundColor: "#8B4389", borderRadius: "20px 20px 0 0", color: "white"}}>
          <div style={{transform: "translate(20px, 10px)", textDecoration: "none"}}>
        {this.props.text}
        </div>
        </div>
      <div style={{width: "90%", height: "60%", backgroundColor: "white", borderRadius: "0 0 20px 20px", padding: "5%"}}>
      
      Submissions
      </div>
      </div>
    )
  }
}


class PortfolioMasterPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {isExtended: "extended"}

  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({isExtended: "not-extended"})
    }
    else {
      this.setState({isExtended: "extended"})
    }
  }
  
  render() {
    return (
      <>
        <div style={{ position: "absolute" }}>
          <Navbar pageName="Your Portfolio" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
            </div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {} : {width: "100vw"})}>
          <div style={{marginLeft: "40px"}}>
            <PointTrackers />
          </div>
          <div style={{width: "100%", height: "93%", position: "relative", marginTop: "3%"}}>
          <div style={{width: "70%", height: "70%", marginTop: "2%",}}>
            <h1 style={{marginLeft: "3%", }}>
              Previous Assignments
            </h1>
            <CustomChonky />
          </div>
          <CheckList />
          </div>
          </div>
          <div>
          </div>
        </div>
      </>
    )
  }
}


class PreviousProjects extends React.Component {
  constructor(props) {
    super(props)
    this.state = {previousProjects: ["Yo", "Yo","Yo", "Yo","Yo", "Yo","Yo", "Yo","Yo", "Yo","Yo", ]}
  }
  render() {
    return (<div style={{overflowY:"auto", height: "90%"}}>
      {this.state.previousProjects.map((item) => {
        return (<div style={{display: "inline-block", marginLeft: "55px", marginTop: "30px"}}>
          <FolderIcon />
          <div>{item}</div>
        </div>)
      })}
    </div>)
  }
}


