import React, { useState } from "react";
import { DragDropContext, Droppable, Draggable, useTouchSensor } from "react-beautiful-dnd";
import {PlussIcon, TrashIcon} from "../../reused-components/icons.js";
import "../../style.css"
import "../../reused-components/navigation.css";
import {updateToDoList, fetchToDoList, getRequestToDoList} from "../../backend.js";
import Calendar from 'react-calendar';
import './Calendar.scss';
import {ToDoEditPopUp} from "./ToDoPopUp.js";




class ToDoAddButton extends React.Component {
  constructor(props) {
    super(props);
    this.state = {isAdding: false, text: ""};
  }
  handleAddTask = () => {
    this.setState((props, state) => {
      return {isAdding: !this.state.isAdding};
    });
  }
  addText = (event) => {
    if (event.keyCode === 13) {
      if (this.state.text !== "") {
        var newState = []
        var tempState = this.props.state[this.props.index];
        tempState.push(
          {
            id: `item-${this.props.state[this.props.index].length + (this.props.index)}-${new Date().getTime()}`,
            content: this.state.text,
            assignedUser: "undefined",
            difficulty: 0,
            hours: 0,
            dueDate: "",
            actionSteps: []
          }
        )
        this.props.state.forEach((content, index) => {
          if (index  === this.props.index) {
            newState.push(tempState)
          }
          else {
            newState.push(content);
          }
        })
        this.state.text="";
        updateToDoList(newState)
        this.props.setState(newState)
      }
    }
    
  }
  changeText = (text) => {
    this.setState((props, state) => {
      return {isAdding: this.state.isAdding, text: text, }
    })
  }
  renderAddSection() {
    if (!this.state.isAdding) {
      return (<button onClick={this.handleAddTask}>
        <div style={{transform: "translateY(10px)"}}><PlussIcon size="15px" /> Add Task</div>
      </button>);
    }
    return (<div>
      <input value={this.state.text} onChange={(text) => this.changeText(text.target.value)} onKeyDown={(e) => this.addText(e)} />
    <div style={{transform: "translateY(10px)"}}>
      <button onClick={() => this.addText({keyCode: 13})} className="add-task-button" style={{width: "80px"}}>
        <p1 className="add-button-text">
          Add Task
        </p1></button>
      <button onClick={this.handleAddTask} className="cancel-button">
      <p1 className="add-button-text">Cancel</p1></button>
      </div>
    </div>);
  }

  render () {
    return (<div>
    {this.renderAddSection()}
    </div>)
  }
}

const reorder = (list, startIndex, endIndex) => {

  const result = Array.from(list);
  const [removed] = result.splice(startIndex, 1);
  result.splice(endIndex, 0, removed);

  return result;
};

/**
 * Moves an item from one list to another list.
 */
const move = (source, destination, droppableSource, droppableDestination) => {
  const sourceClone = Array.from(source);
  const destClone = Array.from(destination);
  const [removed] = sourceClone.splice(droppableSource.index, 1);

  destClone.splice(droppableDestination.index, 0, removed);

  const result = {};
  result[droppableSource.droppableId] = sourceClone;
  result[droppableDestination.droppableId] = destClone;

  return result;
};
const grid = 8;

const getItemStyle = (isDragging, draggableStyle) => ({
  // some basic styles to make the items look a bit nicer
  userSelect: 'none',
  //padding: grid * 2,
  margin: `0 0 ${grid}px 0`,
  width: 170,
  position: "relative",
  // change background colour if dragging
  //background: isDragging ? 'white' : 'white',
  //border: `3px solid #8B4389`, borderRadius: "10px",
  

  // styles we need to apply on draggables
  ...draggableStyle
});

const getListStyle = isDraggingOver => ({
  background: "white",
  padding: grid,
  width: 200,
  marginLeft: "-7px",
  //maxHeight: "340px"
  
  
  //borderRadius: "10px", backgroundColor: "transparent", marginTop: "15px", width: "200px", height: "50px"
});

export function QuoteApp(props) {
  
  
  const toDoNames = ["Backlog", "ToDo", "Doing", "Done"];
  const [state, setState] = useState(props.state);

  function onDragEnd(result) {
    const { source, destination } = result;

    // dropped outside the list
    if (!destination) {
      return;
    }
    const sInd = +source.droppableId;
    const dInd = +destination.droppableId;

    if (sInd === dInd) {
      const items = reorder(state[sInd], source.index, destination.index);
      const newState = [...state];
      newState[sInd] = items;
      updateToDoList(newState)
      setState(newState);
    } else {
      const result = move(state[sInd], state[dInd], source, destination);
      const newState = [...state];

      newState[sInd] = result[sInd];
      newState[dInd] = result[dInd];
      updateToDoList(newState)
      setState(newState);
    }
  }
  function updateToDo(newState) {
    updateToDoList(newState)

    setState(
      newState
    );

  }
  return (

    <div style={{position: "absolute"}}>
      <div style={{ display: "flex" }}>
        <DragDropContext onDragEnd={onDragEnd}>
          
          {state.map((el, ind) =>{ 
            return (<div style={{marginLeft: "50px", userSelect: "none"}}>
              <h1>{toDoNames[ind]}</h1>
              <div>
            <Droppable key={ind} droppableId={`${ind}`}>
              {(provided, snapshot) => (
                <div
                  ref={provided.innerRef}
                  style={getListStyle(snapshot.isDraggingOver)}
                  {...provided.droppableProps}
                >
                  <div style={{ position: "relative", maxHeight: "340px", width: "230px" }} className="scrollingThing">
                  {el.map((item, index) => {
                    return(
                    <Draggable
                      key={item.id}
                      draggableId={item.id}
                      index={index}
                    >
                      {(provided, snapshot) => (
                        <div
                          ref={provided.innerRef}
                          {...provided.draggableProps}
                          {...provided.dragHandleProps}
                          style={getItemStyle(
                            snapshot.isDragging,
                            provided.draggableProps.style
                          )}
                        > 
                        <ToDoItem item={item} state={state} updateToDo={updateToDo} indexes={[ind, index]} changePopUp={props.changePopUp}/>
                        </div>
                      )}
                    </Draggable>
                  )})}
                  </div>
                  {provided.placeholder}
                </div>
              )}
              
            </Droppable></div>
            <ToDoAddButton state={state} setState={setState} index={ind}/>
            </div>
          )})}
        </DragDropContext>
      </div>
    </div>
  );
}

export class ToDoDraggable extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {list: fetchToDoList(), update: false, popUp:false, popUpComponent: null};
     this.refreshToDoList()
  }
  refreshToDoList = async () => {
    var newState = await getRequestToDoList()
    //turns both lists into strings and checks if they are the same, since you can't do that with lists in javascript
    if (makeRegularList(newState) !== makeRegularList(this.state.list)) {
      
      this.setState({list : newState, update: !this.state.update})
    }
    
  }
  componentDidMount() {  
    this.timerID = setInterval(
      () => this.tick(),
      10000
    );
  }

  componentWillUnmount() {
    clearInterval(this.timerID);
  }

  tick() {
   this.refreshToDoList()
  }
  popUp() {
    if (!this.state.popUp) {
      return null;
    }
    return (<><div style={{zIndex: 50}} className="popup-background-div">
      <div style={{zIndex: "5"}} className="popup-clickable-background-div"
    onClick={() => this.changePopUp()}>
    </div>
      <div style={{zIndex: "6"}}>
      {this.state.popUpComponent}
      </div>
    </div>
    </>
      
    )
  }
  changePopUp = (component = null) => {
    this.setState({popUp: !this.state.popUp, popUpComponent: component})
  }
  render() {
    return (<>{this.popUp()}<div key={this.state.update}>
    <QuoteApp state={this.state.list} changePopUp={this.changePopUp}/></div></>)
  }
}
function makeRegularList(lst) {
  var tempList = []
  lst.forEach(list => {
    var tempLst = [];
    list.forEach(item => {
      tempLst.push(item.content + "//>" + item.assignedUser)
    })
    tempList.push(tempLst)
  })
  return JSON.stringify(tempList)
}

class ToDoItem extends React.Component {
  constructor(props) {
    super(props)
    this.state = {assignedUser: this.props.item.assignedUser, item: this.props.item, popUp: false}
  }
  changePopUp = () => {
    this.setState({popUp: false})
    //window.location.reload()
  }
  openPopUp = () => this.props.changePopUp(<ToDoEditPopUp changePopUp={this.props.changePopUp} item={this.props.item}
    updateToDo={this.props.updateToDo} state={this.props.state} indexes={this.props.indexes}/>)
  render() {
    var item = this.state.item
    return (<><div onClick={this.openPopUp}>
      
      <div style={{ top: "0", backgroundColor: "#8B4389", width: "200px", left: "-1px", borderRadius: "10px 10px 0 0",
                          minHeight: "30px", zIndex: "2", border: "solid #8B4389", borderWidth: "2px 2px 0px 2px"}}>
                          
                           
                            
                            <p1 style={{color: "white", marginLeft: "5px"}}>{item.content}</p1>                            
                          </div>
                          <div style={{position: "relative", height: "60px", backgroundColor: "white", width: "190px", borderRadius: "0 0 10px 10px ",
                        border: "solid lightgrey", borderWidth: "0px 2px 2px 2px", padding: "5px"}}>
                            
                          <p1>Difficulty: {item.difficulty}/10</p1> 
                          <div style={{float: "right"}}>
                            {item.hours}
                          <p1 style={{fontWeight: "bold"}}> Hours</p1>
                          </div>
                          <div style={{position: "relative", marginTop: "10px"}} onClick={window.location.reload}>
                            <ActionStepsIcon />
                          <div style={{float: "right"}}><p1 style={{fontWeight: "bold"}}>Due</p1> {item.dueDate}</div>
                          </div>
                          {((item.assignedUser !== "undefined") ? `Assigned User: ${item.assignedUser}` : null)}
                          
                          </div>
                            </div></>) 

  }
}


  
