import React from 'react';
import "../style.css"
import {PointTrackers} from "../reused-components/pointTrackers.js";
import { SideNav, Navbar } from "../reused-components/navigation.js";
import { updatePage } from '../reused-components/PrivateRouter';
import { buyReward, getAvailablePerksAndRewardsList, getUserPerksAndRewardsList } from '../backend';


export class PerksAndRewardsPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = {isExtended: "extended"}
  }
  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState({isExtended: "not-extended"})
    }
    else {
      this.setState({isExtended: "extended"})
    }
  }
  render() {
    return (
      <>
        <div style={{ position: "absolute" }}>
          <Navbar pageName="Perks and Rewards" sideNavAnimation={this.changeState} />
        </div>
        <div style={{ position: "absolute", zIndex: -1 }} className={this.state.isExtended}>
          <div style={{ display: "inline-block", position: "absolute" }}>
            <SideNav isExtended={"extended"} />
            </div>
          <div className="todo-outer-div" style={((this.state.isExtended === "extended") ? {} : {width: "100vw"})}>
          <div style={{marginLeft: "40px"}}>
            <PointTrackers />
          </div>
          <div style={{width: "calc(100% - 40px)", height: "calc(100% - 10vh)", marginTop: "5vh", marginLeft: "40px"}}>
            <PerksAndRewardsMainContainer />
          </div>
          </div>
          <div>
          </div>
        </div>
      </>
    )
  }
}




class PerksAndRewardsMainContainer extends React.Component {
  constructor(props) {
    super(props)
    this.state = {rewardsList: [], boughtRewards: []}
    this.refreshRewardsList()
  }
  refreshRewardsList = async () => {
    var boughtRewards = await getUserPerksAndRewardsList()
    var rewardsList = await getAvailablePerksAndRewardsList()
    this.setState({rewardsList: rewardsList, boughtRewards: boughtRewards})
  }
  render() {
    return <div style={{height: "100%"}} key={JSON.stringify(this.state.rewardsList)}>{[
      this.state.rewardsList,
      this.state.rewardsList,
      this.state.rewardsList,
      this.state.rewardsList
    ].map((item, index) => (
      <div style={{width: "100%", height: "25%"}}>
        <PerksAndRewardsSubContainer content={item} index={index} boughtRewards={this.state.boughtRewards}/>
        </div>)
        )}</div>
  }
}

class PerksAndRewardsSubContainer extends React.Component {
  constructor(props) {
    super(props)
    this.index = this.props.index
    this.state = {content: this.props.content, buyPopUpContent: "", buyPopUpShow: false, index: null, boughtBool: false}
    console.log(JSON.stringify(this.state.content) + "21837198237")
  }
  changeBoughtState = (index) => {
    var newContent = this.state.content;
    newContent[index][0] = !newContent[index][0]
    this.changePopUp()
    this.setState({content: newContent})
  }
  changePopUp = (index = null, boughtBool) =>{
    this.setState({buyPopUpShow: !this.state.buyPopUpShow, index: index, buyPopUpContent: this.state.content[index], boughtBool: boughtBool})
  }
  render() {
    
    return (
      <>
      
      <div style={{border: "1px solid lightgray", height: "100%"}} className="perks-rewards-scrollable">{this.state.content.map((item,index) => {
        if (item.row_id !== this.props.index) {
          return;
        }
        var boughtBool = this.props.boughtRewards.includes(JSON.stringify(item.id))
        return (
      <div style={{width: "130px", cursor: "pointer", display: "inline-block"}} onClick={() => this.changePopUp(index, boughtBool)}>
        <div style={{display: "flex", justifyContent: "center", marginTop: "10px"}}>
        {(true ? <RewardIcon bought={boughtBool} imgUrl={item.image}/>:<StarIcon bought={boughtBool}/> )}
        </div>
        <div style={{textAlign: "center"}}>
          <div style={{display: "block"}}>
            Price: ${item.price}
          </div>
          <div>{item.reward_name}</div>
          
          </div>
          </div>
          
          )})}
          </div>
          <BuyPopUp show={this.state.buyPopUpShow} content={this.state.buyPopUpContent} changePopUp={this.changePopUp} index={this.state.index}
      changeBoughtState={this.changeBoughtState} boughtBool={this.state.boughtBool}
      />
          </>
    
    )
  }
}

class BuyPopUp extends React.Component {
  render() {
    if (!this.props.show) {
      return <></>;
    }
    
    return (<div className="popup-background-div">
      <div className="popup-clickable-background-div"
      onClick={this.props.changePopUp}>
      </div>
      <div style={{ backgroundColor: "white", position: "fixed", zIndex: 10000000, padding: "20px", borderRadius: "20px", minHeight: "400px", minWidth: "400px",
    position: "relative", maxWidth: "40vw"
    }}>
        <div><img src={this.props.content.image} style={{height: "150px", width: "150px", borderRadius: 10000}}/>
        <div style={{fontWeight:"bold"}}>{this.props.content.description_title}</div>
        <div style={{lineHeight: 1.5}}>{this.props.content.description}</div>
        </div>
        <div style={{position: "absolute", bottom: "30px", left: "100px"}}>
        <div style={{marginBottom: "20px"}}>
          {(this.props.boughtBool ? "Do you want to spend?" :"Do you want to buy?")}
        </div>
        <button onClick={() => {
          buyReward(this.props.content.id)
          this.props.changeBoughtState(this.props.index)}} className="add-task-button">
          <p1 className="add-button-text">Confirm</p1>
        </button>
        <button onClick={this.props.changePopUp} className="cancel-button"><p1 className="add-button-text">Cancel</p1></button>
        </div>
      </div>
    </div>)
  }
}




class StarIcon extends React.Component {
  render() {
    return (
    <svg xmlns="http://www.w3.org/2000/svg" height="58" viewBox="0 0 24 24" width="auto" fill={(this.props.bought ?"#000000": "#717171"  )}><path d="M0 0h24v24H0z" fill="none"/><path d="M0 0h24v24H0z" fill="none"/><path d="M12 17.27L18.18 21l-1.64-7.03L22 9.24l-7.19-.61L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21z"/></svg>
  )
  }
}

class RewardIcon extends React.Component {
  render() {
    return (<img src={this.props.imgUrl} style={{height: "58px", width: "58px", borderRadius: 10000, opacity: (this.props.bought? 1: 0.5)}}/>)
  }
}