export class XPTracker extends React.Component {
    render() {
        return (
            <>
            <svg xmlns="http://www.w3.org/2000/svg" width="auto" height={getTrackerHeight()} viewBox="0 0 294 84">
  <rect id="Rectangle_1474" data-name="Rectangle 1474" width="294" height="84" rx="30" fill="#f8f8f8"/>
  <text id="XP" transform="translate(30 54)" font-size="33" font-family="SegoeUI-Semibold, Segoe UI" font-weight="600"><tspan x="0" y="0">XP</tspan></text>
  <foreignObject x="90" y="25" width="160" height="160">
  <div style={{width: "calc(30vh - 60px)"}}>
            <BorderLinearProgress variant="determinate" value={this.props.points}/>
            </div>
            </foreignObject>
            </svg>
            
            </>
        )
    }
}
const BorderLinearProgress = withStyles((theme) => ({
    root: {
      height: 40,
      borderRadius: 20,
    },
    colorPrimary: {
      backgroundColor: "white",
    },
    bar: {
      borderRadius: 20,
      backgroundColor: '#8BEE7C',
    },
  }))(LinearProgress);


export class PointTrackers extends React.Component {
  constructor(props) {
    super(props);
    this.state = {xp: 0, cash: 0, rock: 0, hachet:0, fire: 0, experiment:0}
    this.refreshPoints()
  }
  refreshPoints = async () => {
    var newState = await getUserInfo()
    this.setState(newState)
  }
  render() {
    return (<>
    <div style={{position: "fixed", top: "calc(2vh + 50px)"}}>
              <MainPointTracker points={[this.state.rock,this.state.hachet,this.state.fire,this.state.experiment]}/>
              
            </div>
            <div style={(isMobile?{position: "fixed", top: "calc(8vh + 50px)"}
            :{position: "fixed", top: "calc(2vh + 50px)", right: "15px"})}>
              <div style={{display: "inline-block"}}>
                <XPTracker points={this.state.xp}/>
              </div>
              <div style={{display: "inline-block", marginLeft: "20px"}}>
              <CashTracker points={this.state.cash}/>
              </div>
              </div>
    </>)
  }
}