class JourneyPage extends React.Component {
  constructor(props) {
    super(props)
    this.state = { isExtended: "extended" }
  }

  changeState = () => {
    if (this.state.isExtended === "extended") {
      this.setState((props, state) => {
        return { isExtended: "not-extended" }
      })
    }
    else {
      this.setState((props, state) => {
        return { isExtended: "extended" }
      })
    }
  }
  render() {
    return (<div>
      <div style={{ position: "fixed" }}><Navbar pageName="Your Journey" sideNavAnimation={this.changeState} /></div>
          <div style={{ position: "fixed", zIndex: -1 }} className={this.state.isExtended}>
            <div style={{ display: "inline-block", position: "absolute" }}><SideNav isExtended={"extended"} /></div>
            </div>

    </div>);
  }
}
class RegisterPage extends React.Component {
  
  constructor(props) {
    super(props)
    this.state = {username: "", email: "", password:""}

  }
  handleRegister = async () => {
    const config = {
      headers: {
        'Content-Type': 'application/json',
      },
    };
    await axios.post("https://cambio-api.herokuapp.com/api/auth/register", {username: this.state.username, email: this.state.email, password: this.state.pasword}, config)
  }
  changeUsername = (text) => {
    this.setState((props,state) => {return {username: text}})
  }

  
  render() {
    return (<div style={{height: "100vh", width: "100vw", display: "flex", justifyContent: "center", alignItems: "center"}}>
      <div>
      <input placeholder="Username" onChange={(text) => this.changeUsername(text.target.value)}/>
      <div>
      <input placeholder="email" onChange={(e) => this.setState({email: e.target.value})}/>
      </div>
      <div>
      <input placeholder="password" type="password" onChange={(e) => this.setState({password: e.target.value})}/>
      </div>
      <input placeholder="confirm password" type="password" onChange={(e) => this.setState({Password2: e.target.value})}/>
  
      <button onClick={this.handleRegister}>Register</button>
      </div>
    </div>)
  }
}

class userData extends React.Component {
  constructor(props) {
    super(props)
    this.state = {thing: ""}
    this.updateTest()
    
  }
  updateTest = async () => {
await axios.get('https://cambio-api.herokuapp.com/api/Students/', {
        headers: {
        'Authorization': `token ${localStorage.getItem("token")}`
        }
        })
        .then((response) => {
        console.log(response.data)
        this.setState({thing: JSON.stringify(response.data)})
        })
        .catch((error) => {
        console.error(error)
        })

  }
  render() {
    const queryClient = new QueryClient()
    return (<>
    <QueryClientProvider client={queryClient}>
       <Example />
     </QueryClientProvider>
    <div>{(localStorage.getItem("user"))} {this.state.thing}</div></>)
  }
}
function Example() {
  const { isLoading, error, data } = useQuery('repoData', () =>
    fetch(`https://cambio-api.herokuapp.com/api/Groups/${localStorage.getItem("group")}/`).then(res =>
      res.json()
    )
  )



  return (
    <div>
      {JSON.stringify(data)}
    </div>
  )
}
